export default {
  'url': '/carAnalysis/carType',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'carType|5': [
        {
          'name|+1':['大众','宝马', '奥迪','奔驰','保时捷'],
          'value|0-1000': 1,
          'ratio|0-100':1
        }
      ] 
    } 
  }
}  



