import loader from '@/loader/loader'
import mockApis from './mock'
import './index.css'
import {CarTotal, CarRegister, CarLife, CarRatio, CarPie} from './scripts/index.js'

const CarTotalData = new CarTotal()
const CarRegisterData = new CarRegister()
const CarLifeData = new CarLife()
const CarRatioData = new CarRatio()
const CarPieData = new CarPie()

loader.load({
  apis: mockApis,
  init() {
    CarTotalData.init()
    CarRegisterData.init()
    CarLifeData.init()
    CarRatioData.init()
    CarPieData.init()
  }
})


