export default {
  'url': '/carAnalysis/carLife',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'carLife|4': [
        {
          'name|+1':['6年以上', '3-6年', '1-3年', '1年以内'],
          'value|1-10000': 1
        }
      ] 
    } 
  }
}


