import { fetch } from '@/util/request'
import { CarRegister } from '@/charts/index.js'

const carRegister = new CarRegister('#register-con')

export default class rgisterData{
  init() {
    fetch('fetchCarRegister', (data) => {
      carRegister.renderPage(data.carRegister)
    }) 
  }
}


