export default {
  'url': '/carAnalysis/carTotal',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'carTotal|4': [
        {
          'name|+1':['机动车总数', '网约车总数', '重点车总数', '货车总数'],
          'value|1-100000': 1,
          'unit':'辆'
        }
      ] 
    } 
  }
}


