/**
 * @Author:      zhaojh
 * @DateTime:    2018-01-11 09:11:17
 * @Description: 全市交通违规数量同比/环比
 * @Last Modified By:   zhaojh
 * @Last Modified Time:    2018-01-11 09:20:10
 */
import d3 from 'd3'
import $ from 'jquery'
import getContainer from '../tool/getContainer'
import getSize from '../tool/getSize'
import getLinearGradient from '../tool/getLinearGradient'

export default class Area {
  // 默认配置项
  defaultSet() {
    return {
      width: 1400,
      height: 300,
      margin: {
        top: 20,
        right: 20,
        bottom: 30,
        left: 40
      },
      itemStyle: {
        tongbi: {
          x1: 0,
          y1: 0,
          x2: 0,
          y2: 100,
          colorStops: [{
            offset: 0,
            color: 'rgb(18,0,255)',
            opacity: 0.8
          }, {
            offset: 100,
             color: 'rgb(70,170,255)',
            opacity: 0.2
          }]
        },
        huanbi: {
          x1: 0,
          y1: 0,
          x2: 0,
          y2: 100,
          colorStops: [{
            offset: 0,
            color: 'rgb(226,255,68)',
            opacity: 0.8
          }, {
            offset: 100,
            color: 'rgb(68,255,134)',
            opacity: 0.2
          }]
        }
      },
      xAxis: {
        color: '#46aaff',
        fontSize: 24,
        anchor: 'middle',
        mtop: 30 // 顶部偏移量
      },
      yAxis: {
        color: '#a4d5ff',
        fontSize: 20,
        mleft: 50 // 往右侧偏移量
      }
    }
  }
  // 初始化，创建容器
  constructor(selector, option) {
    const defaultSet = this.defaultSet()
    this.config = Object.assign(defaultSet, option)
    this.chartName = 'l-area-chart'
    const {
      width,
      height,
      itemStyle: {
        tongbi,
        huanbi
      },
      margin: {
        top,
        bottom
      }
    } = this.config
    this.size = getSize(selector, width, height)
    const [w, h] = this.size

    this.svg = getContainer(selector, this.chartName, 'svg').attr({
      'width': w,
      'height': h
    })
    // 创建面积图的线性渐变
    const tbGradient = getLinearGradient(tongbi)
    const hbGradient = getLinearGradient(huanbi)
    const defs = getContainer(`.${this.chartName}`, `${this.chartName}-defs`, 'defs')
    defs.html(`${defs.html()}${tbGradient.dom}${hbGradient.dom}`)
    this.tbGradientId = tbGradient.id
    this.hbGradientId = hbGradient.id

    // 创建蒙版
    const mask = getContainer(`.${this.chartName}-defs`, `${this.chartName}-mask`, 'mask')
    mask.attr('id', `${this.chartName}-mask`).attr('maskUnits', 'userSpaceOnUse')
    const maskRect = getContainer(`.${this.chartName}-mask`, `${this.chartName}-mask-rect`, 'rect')
    maskRect.attr({
      'x': 0,
      'y': 0,
      'fill': 'white',
      'fill-opacity': 1
    })

    // 中线距离
    this.distanceMid = (h - bottom - top) / 2 + top
  }
  // 绘制
  render(data) {
    const {
      margin: {
        top,
        right,
        bottom,
        left
      }
    } = this.config
    const [width, height] = this.size

    this.xScale = d3.scale.ordinal()
      .domain(d3.range(data.length))
      .rangeRoundBands([20, width - right - left], 0.9)

    const maxRatio = Math.max(
      d3.max(data, function (d) {
        return d.huanbi
      }),
      d3.max(data, function (d) {
        return d.tongbi
      })
    )
    const minRatio = Math.min(
      d3.min(data, function (d) {
        return d.huanbi
      }),
      d3.min(data, function (d) {
        return d.tongbi
      })
    )
    const targetRatio = Math.floor(
      Math.max(
        Math.abs(maxRatio) * 1.2,
        Math.abs(minRatio) * 1.2
      )
    )
    this.yRatioScale = d3.scale.linear()
      .domain([-targetRatio, targetRatio])
      .range([0, height - bottom - top])

    // 绘制面积图
    this.drawTBArea(data)
    this.drawHBArea(data)

    // 绘制Y轴
    this.drawYaxis(targetRatio)
    // 绘制X轴
    this.drawXaxis(data)

    // 图例事件
    this.event()

    // 面积动画--利用蒙版
    d3.select(`#${this.chartName}-mask`).select('rect')
      .attr({
        'width': 0,
        'height': height
      })
      .transition()
      .delay(500)
      .duration(3800)
      .ease('EASE')
      .attr({
        'width': width
      })
  }
  // 绘制同比面积图
  drawTBArea(data) {
    const self = this
    const {
      margin: {
        bottom
      },
      itemStyle: {
        color
      }
    } = this.config
    const [width, height] = this.size
    const container = getContainer(`.${this.chartName}`, 'area-container', 'g')
    const area = getContainer('.area-container', 'area-path-tongbi', 'path')
    const areaPath = d3.svg.area()
      .x((d, i) => {
        return this.xScale(i)
      })
      .y1((d, i) => {
        return this.yRatioScale(d.tongbi)
      })
      .y0(height - bottom)

    area.attr({
      'd': areaPath(data),
      'stroke': 'none',
      'fill': `url(#${this.tbGradientId})`,
      'transform': `translate(-${this.xScale.rangeBand()},0)`,
      'mask': `url(#${this.chartName}-mask)`
    })
  }
  // 绘制环比面积图
  drawHBArea(data) {
    const self = this
    const {
      margin: {
        bottom
      },
      itemStyle: {
        color
      }
    } = this.config
    const [width, height] = this.size
    const container = getContainer(`.${this.chartName}`, 'area-container', 'g')
    const area = getContainer('.area-container', 'area-path-huanbi', 'path')

    const areaPath = d3.svg.area()
      .x((d, i) => {
        return this.xScale(i)
      })
      .y1((d, i) => {
        return this.yRatioScale(d.huanbi)
      })
      .y0(height - bottom)

    area.attr({
      'd': areaPath(data),
      'stroke': 'none',
      'fill': `url(#${this.hbGradientId})`,
      'transform': `translate(-${this.xScale.rangeBand()},0)`,
      'mask': `url(#${this.chartName}-mask)`
    })
  }
  // 绘制Y轴
  drawYaxis(targetRatio) {
    const {
      margin: {
        left,
        right,
        top,
        bottom
      },
      yAxis: {
        fontSize,
        color,
        mleft
      }
    } = this.config
    const [width, height] = this.size
    var yRatioAxisData = [-targetRatio, -Math.floor(targetRatio / 2), 0, Math.floor(targetRatio / 2), targetRatio]
    var yRatioAxis = getContainer(`.${this.chartName}`, 'y-ratio-axis', 'g') 
    var updateRatio = yRatioAxis.selectAll('text').data(yRatioAxisData)
    updateRatio.enter().append('text')
    updateRatio.exit().remove()
    yRatioAxis.selectAll('text').data(yRatioAxisData).attr({
        'x': left + mleft,
        'y': function (d, i) {
          return height - (top + i * (height - top - bottom) / 4) - 14 // 向上偏移字体大小的一半
        },
        'text-anchor': 'end',
        'fill': color,
        'font-size': fontSize,
        'font-family': 'sans-serif'
      })
      .text(function (d, i) {
        return d + '%'
      })
  }
  // 绘制X轴
  drawXaxis(data) {
    const {
      margin: {
        right,
        bottom,
        left
      },
      xAxis: {
        color,
        fontSize,
        anchor,
        mtop
      },
      yAxis: {
        mleft
      }
    } = this.config
    const [width, height] = this.size
    const xAxisDom = getContainer(`.${this.chartName}`, 'x-axis', 'g')
    const update = xAxisDom.selectAll('text').data(data)
    update.enter().append('text')
    update.exit().remove()
    xAxisDom.selectAll('text').data(data).attr({
        'x': (d, i) => {
          return this.xScale(i)
        },
        'y': height - bottom + mtop,
        'text-anchor': anchor,
        'font-size': fontSize,
        'fill': color
      })
      .text((d, i) => {
        return d.name
      })
    // 添加一根水平线
    let xAxisLine = getContainer(`.${this.chartName}`, 'x-axis-line', 'line')
    xAxisLine.attr({
      'x1': left + 40,
      'x2': width - left - right - 100,
      'y1': height - bottom,
      'y2': height - bottom,
      'stroke': '#637dff',
      'fill': 'none'
    })
  }
  event() {
    $('.legend-btn').off().on('click',function(e) {
      const type = $(e.target).data('radio')
      $('.act-con').find('.area-path' + type).fadeToggle()
    })
  }
}



