// import $ from 'jquery'
import { fetch } from '@/util/request'
import { CarRatio } from '@/charts/index.js'
const carRatio = new CarRatio('#act-con', {})

export default class ratioData{
  init() {
    fetch('fetchCarRatio', (data) => {
      carRatio.render(data.carRatio)
    }) 
  }
}


