/*
 * @Author: ouyangdecai@hiynn.com 
 * @Date: 2017-12-27 14:19:26 
 * @Description: 危险指数组件的截断条形图
 * @Last Modified by: ouyangdecai@hiynn.com
 * @Last Modified time: 2017-12-27 15:58:32
 */
import * as d3 from 'd3'
import _ from 'lodash'
import bg from './images/bg.png'
class SplitBar{
  /**
   * 默认配置项
   * @return {object} 配置对象
   */
  defaultSetting() {
    return {
      height: 345,
      width: 380
    }
  }
  constructor(opt){
    const defaultSetting = this.defaultSetting()
    this.config = _.merge({},defaultSetting, opt)
    this.drawLegend = this.drawLegend.bind(this)
    this.addMask = this.addMask.bind(this)
    this.svg = d3.select('.danger-ratio').select('svg')
    this.svg.append('g').attr('class', 'data-g')
    this.svg.append('g').attr('class', 'legend')
    this.drawLegend()
    this.addMask()
  }
  drawLegend(){
    const { svg } = this
    const legend = svg.select('.legend')
    legend.append('rect')
      .attr('x',46)
      .attr('y',300)
      .attr('height',15)
      .attr('width',15)
      .attr('fill', '#ff9c00')
    legend.append('text')
      .attr('x',74)
      .attr('y',315)
      .attr('fill', '#fff')
      .attr('font-size',18)
      .text('人流指数')
    legend.append('rect')
      .attr('x',198)
      .attr('y',300)
      .attr('height',15)
      .attr('width',15)
      .attr('fill', '#0aff55')
    legend.append('text')
      .attr('x',222)
      .attr('y',315)
      .attr('fill', '#fff')
      .attr('font-size',18)
      .text('车流指数')

  }
  /**
   * 添加蒙版
   * @return {undefined}
   */
  addMask(){
    const gridLineCount = 80
    const { svg, config } = this
    const { height, width } = config
    // 添加蒙版
    const mask = svg.select('defs').append('mask')
      .attr('id','grid-mask')
      .attr('x', 0)
      .attr('y', 0)
      .attr('width', 1)
      .attr('height', 1)
    mask.append('rect')
      .attr('fill', '#fff')
      .attr('x', 0)
      .attr('y', 0)
      .attr('width', width)
      .attr('height', height)
    const perHeight = width / gridLineCount
    for(let i = 0; i < gridLineCount; i++){
      mask.append('line')
        .attr('x1', perHeight * i)
        .attr('y1', 0)
        .attr('x2', perHeight * i)
        .attr('y2', 270)
        .attr('stroke', '#000')
        .attr('stroke-width', 3)
    }
  }
  /**
   * 渲染图形
   * @param {any} data 从后端请求的数据
   * @return {undefined}
   */
  render(data){
    const { svg } = this
    const maxPerson = d3.max(data, d => {
      return d.person
    })
    const maxVehicle = d3.max(data, d => {
      return d.vehicle
    })
    const update = svg.select('.data-g').selectAll('.item').data(data)
    const enter = update.enter().append('g').attr('class', 'item')
    enter.append('image')
      .attr('xlink:href', bg)
      .attr('x', 0)
      .attr('y', 0)
      .attr('height', 35)
      .attr('width', 300)
    enter.append('text').attr('class', 'name')
    enter.append('rect').attr('class', 'person')
    enter.append('rect').attr('class', 'vehicle')
    enter.append('text').attr('class', 'person-value')
    enter.append('text').attr('class', 'vehicle-value')
    
    update
      .attr('transform', (d,i) => {
        return `translate(20, ${i * 55 + 20})`
      })

    update.select('.name')
      .attr('x',40)
      .attr('y',23)
      .attr('text-anchor', 'middle')
      .attr('font-size', 18)
      .attr('fill', '#aff5ff')
      .text(d => {
        return d.name
      })
    update.select('.person')
      .attr('fill','url(#person-gradient)')
      .attr('x', 93)
      .attr('y', 3)
      .attr('height', 12)
      .attr('width', d => {
        return d.person / maxPerson * 180
      })
      .attr('mask','url(#grid-mask)')
    update.select('.vehicle')
      .attr('fill','url(#vehicle-gradient)')
      .attr('x', 93)
      .attr('y', 20)
      .attr('height', 12)
      .attr('width', d => {
        return d.vehicle / maxVehicle * 180
      })
      .attr('mask','url(#grid-mask)')
    update.select('.person-value')
      .attr('x',300)
      .attr('y',15)
      .attr('text-anchor', 'end')
      .attr('font-size', 12)
      .attr('fill', '#fff')
      .text(d => {
        return d.person
      })
    update.select('.vehicle-value')
      .attr('x',300)
      .attr('y',32)
      .attr('text-anchor', 'end')
      .attr('font-size', 12)
      .attr('fill', '#fff')
      .text(d => {
        return d.vehicle
      })
  }
}
export default SplitBar



