import { fetch } from '@/util/request'
import { CarLife } from '@/charts/index.js'
const carLife = new CarLife('#carlift-con')

export default class lifeData{
  init() {
    fetch('fetchCarLife', (data) => {
      carLife.renderPage(data.carLife)
    }) 
  }
}


