import d3 from 'd3'
import _ from 'lodash'
import { isNoData } from '../util/util'
export default class BaseDatas {
  /**
   * 饼图默认配置项
   * @return {object} 默认配置项
   */
  defaultSetting () {
    const width = 550
    const height = 400
    const svgStyle = {
      padding: {
        top: 50,
        left: 50,
        bottom: 50,
        right: 50 
      }
    }
    return{
      width, height, svgStyle 
    }
  }
  /**
   * 组件配置项
   * @param {string} selector 容器元素选择器
   * @param {object} opt 图表组件配置项
   */
  constructor(selector, opt) {
    let defaultSetting = this.defaultSetting()
    this.config = _.merge({}, defaultSetting, opt)
    let { width, height, svgStyle } = this.config  
    let { top, bottom } = svgStyle.padding
    // 创建svg元素
    let svg = d3.select(selector)
      .append('svg')
      .attr('width', width + top)
      .attr('height', height + bottom)
      .style('margin-top', 15)
    this.svg = svg 
    
    // 创建包裹背背景矩形的g元素
    this.rectBG = svg.append('g')
        .attr('transform', 'translate(' + 0 + ',' + 0 + ')') 
    
    // 创建包裹矩形的g元素
    this.rect = svg.append('g') 
        .attr('transform', 'translate(' + 0 + ',' + 0 + ')')
    
  }
  /**
   * 渲染页面  出口文件
   * @param {Object} self 
   */
  renderPage(data) {
    let self = this
    let { width, height, svgStyle} = this.config  
    let {
      left: paddingLeft,
      right: paddingRight
      
    } = svgStyle.padding
    
    // 判断数据是否为空
    if(!data || !data.length) {
      isNoData(self.svg, { width, height })
      return false
    }
    let dataset = []
    let gridArray = data.map(function(d){
      dataset.push(d)
      return [d.name, d.value]
    })
    // x轴值域最大值
    let xMax = d3.max(gridArray, function(d) {
      return d[1]
    })
    // 比例尺
    self.xRuler = d3.scale.linear()
      .domain([0, xMax * 1.2])
      .range([0, width - paddingLeft - paddingRight])
    
    self.yRuler = d3.scale.ordinal()
      .domain(gridArray.map(function(val) {
        return val.name
      }))
      .rangeRoundBands([0, height - paddingLeft - paddingRight], 2)

    // 绘制矩形、文字、数字
    this.chartsRect(dataset)   
  }
  /**
   *  绘制矩形
   */
  chartsRect(data) { 
    let self = this
    let {svgStyle} = this.config  
    let {
      // top: paddingTop,
      left: paddingLeft
    } = svgStyle.padding
    
    // 定义矩形宽度
    let rectStep = 85 // 包括空白
    let rectWidth = 5 // 不包括空白
    let color = ['#a340ee', '#1dd7ff', '#5efcbf', '#ffff00']

    // 处理数据
    let dataset = []
    data.map(function(d){
      dataset.push(d)
      return [d.name, d.value]
    })

    // 绘制背景框
    self.rectBG
      .selectAll('rect')
      .data(dataset)
      .enter()
      .append('rect')
      .attr('fill','#0c3453')
      .attr('y',function(d, i){
        return paddingLeft + i * rectStep + 22
      })
      .attr('x',0)
      .attr('width', 380)
      .attr('height', rectWidth)  
    
    // 绘制矩形
    self.rect
      .selectAll('rect')
      .data(dataset)
      .enter()
      .append('rect')
      .attr('fill',(d, i) => {
        return color[i]
      })
      .attr('y',function(d, i){
        return paddingLeft + i * rectStep + 20
      })
      .attr('x',0)
      .attr('width', 0)
      .attr('height', rectWidth)
      .transition()
      .delay(500)
      .duration(3000)
      .attr('width',(d) => {
           return self.xRuler(d.value)
      })
  }
}



