import { fetch } from '@/util/request'
import { CarPie } from '@/charts/index.js'
const carPie = new CarPie('#card-con')

export default class pieData{
  init() {
    fetch('fetchCarType', (data) => {
      carPie.renderPage(data.carType)
    }) 
  }
}


