export default {
  'url': '/carAnalysis/carRegister',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'carRegister|10': [
        {
          'name|+1':['梁平', '大渡口', '北碚', '江北' ,'南岸', '渝北', '渝中', '沙坪坝' ,'城口', '永川'],
          'value|1-10000': 1
        }
      ] 
    } 
  }
}


