import $ from 'jquery'
import d3 from 'd3'
import { fetch } from '@/util/request'
import { CarBar } from '@/charts/index.js'
import cartotal from '../templates/carTotal.hbs'
const carBar = new CarBar('#car-bar')

export default class carData{
  init() {
    fetch('fetchCarTotal', {
      code: 1
    }, (data) => {
      $('#car-list').html(cartotal(data.carTotal))
       carBar.renderPage(data.carTotal)
    })
  }
} 




